@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  background-color: #1d1d1d;
  color: #fff;
  margin: 0;
  padding: 0;
}
.App {
  background-color: #1d1d1d !important;
  color: #919191 !important;
  /* position: fixed; */
  top: 0;
  left: 0;
  width: 100%;
  /* z-index: 1000; */
  font-family: "Roboto", sans-serif;
  padding-top: 60px;
  /* position: relative; */
}

ul{
  list-style: none;
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
  color: #fff;
}

.clearfix:before,
.clearfix:after {
    content: "";
    display: table;
}

.clearfix:after {
    clear: both;
}



.container {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
}

.navbar-wrapper {
    width: 100%;
    height: 60px;
    background-color: #1D1D1D;
    border-bottom: 1px solid #2f2f2f;
    position: fixed;
    z-index: 20;
    left: 0;
    right: 0;
    top: 0;
    display: block;
}

.nav-logo a {
    float: left;
    height: inherit;
}

.nav-logo a img {
    margin: 14px 30px 0 15px;
}

.navbar-wrapper .main-nav-links {
    float: right;
    margin-right: 15px;
}

.quick-search {
    float: left;
    margin-top: 10px;
}

.quick-search-container {
    position: relative;
    margin-right: 10px;
}

#quick-search-input {
    outline: none;
    width: 250px;
    height: 40px;
    font-size: 16px;
    font-weight: 500;
    padding: 4px 30px;
    color: #919191;
    border: 2px solid #333;
    border-radius: 20px;
    background: #1D1D1D url(/yts-movie-app/static/media/icon-search.a4282c8f.svg) no-repeat 9px 11px;
}

.nav-links {
    display: inline-block;
    padding-top: 20px;
}

.nav-links li {
    display: inline;
    margin-left: 20px;
}

.nav-links li a {
    font-size: 16px;
    font-weight: 500;
    color: #919191;
    -webkit-transition: color .25s ease-in-out;
    transition: color .25s ease-in-out;
}

.nav-links li a:hover {
    color: #fff;
}

.nav-links li a.active {
    color: #6AC045;
}

.nav-links-guest {
    display: none;
}

.nav-links-guest a {
    color: #fff !important;
}

.nav-links-guest a:hover {
    color: #919191 !important;
}

/* ---------------HERO SECTION-------------------------- */

.background-img {
    position: relative;
    background: url(/yts-movie-app/static/media/background.eeda8ce4.jpg) no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    padding: 0;
    left: 0;
    right: 0;
    z-index: 1;
    display: block;
}

.hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0, rgba(29, 29, 29, 0.295)), to(#0f0f0f));
    background: -webkit-linear-gradient(top, rgba(29, 29, 29, 0.295) 0, #0f0f0f);
    background: linear-gradient(180deg, rgba(29, 29, 29, 0.295) 0, #0f0f0f);
}

.hero-section {
    width: 100%;
}

.hero-section .hero-title {
    text-align: center;
    color: #fff;
    padding-top: 50px;
}

.hero-title h1 {
    font-size: 46px;
    font-weight: 700;
    text-shadow: 0 2px 2px rgba(0, 0, 0, .4);
    margin-bottom: 15px;
    margin-top: 0;
}

.hero-title p {
    font-size: 15px;
    color: #ccc;
    max-width: 650px;
    margin: 0 auto;
    line-height: 25px;
    margin-bottom: 30px;
}

/* -------------------MOVIES SECTION------------------------ */

.row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.browse-movie-wrap {
    position: relative;
    width: 100%;
    flex-basis: 20%;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 15px;
}

.browse-movie-link {
    max-width: 220px;
    border: 5px solid #fff;
    border-radius: 4px;
    font-size: 24px;
    overflow: hidden;
    position: relative;
    transition: border .15s ease-in-out;
    -moz-transition: border .15s ease-in-out;
    -webkit-transition: border .15s ease-in-out;
    cursor: pointer;
    height: 250px;
    min-height: 350px;
}

.browse-movie-link img {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
}

/* ------------------OVERLAY CARD------------------- */

.browse-movie-link .overlay {
    position: absolute;
    text-align: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.801);
    color: #fff;
    padding-top: 40px;
    left: 0;
    right: 0;
    top: 0;
    z-index: 2;
    opacity: 0;
    transition: all 300ms;
    -webkit-transition: all 300ms;
    -moz-transition: all 300ms;
    transition-delay: 100ms;
    -webkit-transition-delay: 100ms;
    -moz-transition-delay: 100ms;
    cursor: pointer;
}

.overlay .star-icon img {
    color: #6AC045;
    width: 24px;
    height: 24px;
    position: relative;
    margin: 0 auto;
}

.overlay h4 {
    font-size: 25px;
    margin: 0;
    position: relative;
    width: 100%;
    font-weight: 700;
    color: #fff;
    line-height: 1.5em;
}

.overlay .rating {
    margin-top: 10px;
    margin-bottom: 20px;
}

.overlay button {
    background-color: #5da93c;
    color: #fff;
    margin-top: 40px;
    padding: 10px 0;
    border: none;
    font-weight: 700;
    font-size: 16px;
    position: relative;
    top: 40px;
    width: 130px;
    border-radius: 5px;
    transition: all 300ms ease;
    -webkit-transition: all 300ms ease;
    -moz-transition: all 300ms ease;
    cursor: pointer;
}

.overlay button:hover {
    background-color: #50a72b;
}

.browse-movie-link:hover .overlay {
    opacity: 1;
}

.browse-movie-link:hover .overlay button {
    -webkit-transform: translateY(-40px);
    transform: translateY(-40px);
}

.browse-movie-link:hover {
    border: 5px solid #6ac045;
}

/* -------OVERLAY CARD END------------- */

.browse-movie-bottom {
    margin: 0;
    padding: 5px;
    width: 200px;
    text-align: left;
}

.browse-movie-bottom a {
    color: #fff;
    letter-spacing: .5px;
    display: block;
    font-weight: 600;
    transition: color .15s ease-in-out;
    -moz-transition: color .15s ease-in-out;
    -webkit-transition: color .15s ease-in-out;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.6em;
}

.browse-movie-bottom a:hover {
    color: #919191;
}

.popular-section {
    margin-top: 80px;
    padding-bottom: 15px;
    border-top: 1px solid #817C7C;
}

.popular-section h2 {
    color: #fff;
    padding: 8px;
    text-align: center;
    width: 200px;
    font-size: 18px;
    font-weight: 700;
    position: relative;
    top: -22px;
    margin: 0 auto;
    background-color: #6AC045;
    border-radius: 20px;
    margin-bottom: 20px;
}

.popular-section h2 img {
    position: relative;
    top: 2px;
    height: 18px;
    width: 18px;
    margin-right: 10px;
}

/* ----------- MAIN SECTION ------------------ */

.main-section {
    width: 100%;
    background-color: #101010;
    position: relative;
}

.latest-section {
    margin: 0;
    padding-top: 20px;
}

.latest-section .content {
    margin-top: 30px;
    padding-bottom: 30px;
    border-top: 1px solid #817C7C;
}

.latest-section h2 {
    color: #fff;
    width: 240px;
    padding: 8px 0;
    text-align: center;
    font-size: 18px;
    font-weight: 700;
    position: relative;
    top: -22px;
    margin: 0 auto;
    background-color: #101010;
    margin-bottom: 20px;
}

.latest-section .view-all a {
    color: #6AC045;
    -webkit-transition: all .3s;
    transition: all .3s;
}

.latest-section .view-all a:hover {
    color: #fff;
}

/* --------------------FOOTER------------------- */

footer {
    width: 100%;
    background-color: #171717;
    text-align: center;
    position: relative;
    z-index: 20;
    bottom: 0;
    border-top: 1px solid #2f2f2f;
}

footer ul {
    display: inline-block;
    margin: 10px 10px 0;
    padding: 10px;
}

footer ul li {
    display: inline;
    margin-right: 8px;
    color: #fff;
}

footer ul li a, footer p a {
    color: #919191;
    -webkit-transition: color .25s ease-in-out;
    transition: color .25s ease-in-out;
}

footer ul li a:hover, footer p a:hover {
    color: #fff;
}

footer p {
    margin-top: 8px;
}

/* -------------------------------MEDIA QUERIES---------------------------- */

@media only screen and (max-width: 1024px) {
    .container {
        max-width: 992px;
    }
    .browse-movie-bottom {
        width: 170px;
    }
    .browse-movie-link {
        min-height: 270px;
    }
    .hero-title h1 {
        font-size: 36px;
        font-weight: 600;
    }
    .hero-title p {
        font-size: 15px;
        max-width: 700px;
    }
    .browse-movie-link .overlay {
        padding-top: 30px;
        display: none;
    }
    .overlay h4 {
        font-size: 20px;
    }
}

@media only screen and (max-width: 768px) {
    .container {
        max-width: 720px;
    }
    .browse-movie-wrap {
        flex-basis: 20%;
        margin-top: 15px;
    }
    .browse-movie-link {
        min-height: 250px;
    }
    .browse-movie-bottom {
        width: 150px;
    }
    .browse-movie-bottom a, .browse-movie-bottom div {
        font-size: 14px;
    }
    .browse-movies-section .pagination li {
        padding: 10px 0px;
    }
    .browse-movies-section .movies-list {
        margin-top: 10px;
    }
}

@media only screen and (max-width: 650px) {
    .container {
        max-width: 540px;
    }
    .nav-logo a img {
        margin-right: 0;
    }
    .hero-title h1 {
        font-size: 28px;
        font-weight: 600;
        text-align: left;
        line-height: 1.1em;
    }
    .hero-title p {
        font-size: 14px;
        max-width: 400px;
        line-height: 1.5em;
        text-align: left;
    }
    .hero-title {
        margin-left: 20px;
        margin-right: 10px;
    }
    .browse-movie-wrap {
        flex-basis: 45%;
        padding: 0;
    }
    .popular-section {
        margin-top: 60px;
    }
    .popular-section h2 {
        width: 180px;
        font-size: 16px;
        margin-bottom: 0px;
    }
    .popular-section h2 img {
        height: 16px;
        width: 16px;
        margin-right: 8px;
    }
    .browse-movie-wrap {
        margin-top: 15px;
    }
    .browse-movie-link{
        min-height: 300px;
    }
    .latest-section {
        padding-top: 15px;
    }
    .latest-section h2 {
        font-size: 16px;
        width: 220px;
        margin-bottom: -10px;
    }
    .browse-movies-section .pagination {
        display: none;
    }
    .browse-movies-section .hidden-pagination {
        display: flex;
    }
}

@media only screen and (max-width: 420px) {
    .browse-movies-section .main-search {
        height: 150px;
    }
    .browse-movies-section .search-form {
        padding: 0;
        margin-top: 0;
    }
    .search-form .search-field {
        width: 90%;
        display: block;
    }
    .search-field {
        margin-left: auto;
        margin-right: auto;
    }
    .search-field p {
        font-size: 22px !important;
    }
    .search-field input {
        height: 40px !important;
    }
    .search-form .search-btn {
        margin: 0 auto;
        margin-left: 20px !important;
    }
    .search-btn input {
        padding: 10px 15px !important;
    }
}

@media only screen and (max-width: 330px) {
    .browse-movie-wrap {
        flex-basis: 70%;
    }
    .browse-movie-bottom {
        width: 220px;
    }
}
.loading-screen {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 10000;
    background-color: #1d1d1d;
    display: flex;
    align-items: center;
    justify-content: center;
}

.box {
    width: 150px;
    text-align: center;
}

.box .spinner {
    width: 64px;
    height: 64px;
    margin: 0 auto;
    background: url(/yts-movie-app/static/media/25.202ea67c.svg) no-repeat center center;
}

.box p {
    margin-left: 30px;
    margin-top: 8px;
    font-weight: 600;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
    background: -webkit-linear-gradient(#eee, #73d14a);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}
.browse-movies-section {
    width: 100%;
    position: relative;
}

.main-search {
    position: relative;
    width: 100%;
    background: url(/yts-movie-app/static/media/background.eeda8ce4.jpg);
    background-size: cover;
    background-color: #171717;
    padding: 30px 0;
    height: 250px;
    z-index: 2;
}

.search-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: #17171796;
}

.search-form {
    width: 100%;
    max-width: 900px;
    margin: 0 auto;
    padding: 40px 10px;
    margin-top: 20px;
}

.search-field {
    position: relative;
    margin-top: 10px;
    margin-bottom: 20px;
    width: 80%;
    display: inline-block;
    overflow: hidden;
}

.search-field p {
    margin: 0;
    margin-bottom: 10px;
    font-size: 24px;
    font-weight: 500;
}

.search-field input[type=text] {
    background-color: transparent;
    border: 1px solid rgba(255, 255, 255, 0.212);
    border-radius: 4px;
    width: 100%;
    height: 50px;
    padding: 12px;
    font-size: 16px;
    line-height: 1.5em;
    color: #fff;
    box-sizing: border-box;
    outline: none;
}

.search-btn {
    display: inline-block;
    vertical-align: top;
    margin-top: 50px;
    margin-left: 10px;
}

.search-btn input {
    border: 0;
    border-radius: 4px;
    color: #fff;
    background-color: #6ac045;
    font-size: 16px;
    font-weight: 600;
    outline: none;
    padding: 14px;
    max-width: 100px;
    cursor: pointer;
}

.search-btn button:hover {
    background-color: #52a52f;
}

.movies-list {
    margin-top: 40px;
    margin-bottom: 40px;
}

.movie-count {
    margin: 50px 0;
    color: #52a52f;
    font-size: 22px;
    text-align: center;
}

/* --------------------PAGINATION------------------------- */

.pagination {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
}

.hidden-pagination {
    display: none;
}

.pagination li {
    border: 1px solid #333;
    border-radius: 4px;
    margin-right: 5px;
    padding: 10px;
    -webkit-transition: all .3s;
    transition: all .3s;
}

.pagination li a {
    padding: 10px;
}

.pagination .active-page, .pagination li:hover {
    background-color: #52a52f;
}
.movie-detail-container {
    padding-top: 60px;
}

.hidden-movie-title {
    display: none;
    z-index: 3;
    position: relative;
    color: #fff;
}

.hidden-movie-title h1 {
    margin-left: 65px;
    font-size: 38px;
}

.background-image {
    z-index: 1;
    display: block;
    background-size: cover!important;
}

.background-image, .background-overlay {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    top: 60px;
    height: 600px;
}

.background-overlay {
    z-index: 2;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0, rgba(29, 29, 29, 0.507)), to(#1d1d1d));
    background: -webkit-linear-gradient(top, rgba(29, 29, 29, 0.507) 0, #1d1d1d);
    background: linear-gradient(180deg, rgba(29, 29, 29, 0.507) 0, #1d1d1d);
}

.movie-container, .summary-section {
    position: relative;
    z-index: 3;
    flex-wrap: nowrap;
    align-items: stretch;
}

.movie-container .movie-poster {
    width: 25%;
    float: left;
    position: relative;
    padding-left: 15px;
    padding-right: 15px;
}

.card-container {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card-container .card {
    width: 100%;
}

.card .image-container {
    display: block;
    border: 6px solid #fff;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    min-height: 300px;
}

.card .image {
    width: 100%;
    display: block;
}

.movie-poster .download-btn {
    width: 100%;
    background-color: #75c74e;
    color: #fff;
    border: none;
    outline: none;
    border-radius: 3px;
    padding: 8px;
    margin: 0 auto;
    font-size: 16px;
    font-weight: 600;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    cursor: pointer;
}

.movie-poster .download-btn:hover {
    background-color: #59a336;
}

.movie-info {
    width: 40%;
    margin-left: 5%;
    color: #fff;
    float: left;
}

.movie-info h1 {
    margin-top: 0;
    font-size: 38px;
}

.movie-info h2 {
    margin: 0;
    font-size: 20px;
    line-height: 1.5em;
}

.movie-info .torrent-quality {
    margin-top: 55px;
}

.torrent-quality div {
    margin-top: 10px;
}

.torrent-quality .download-torrent {
    background: rgba(27, 27, 35, .4);
    padding: 5px 10px;
    border-radius: 3px;
    border: 1px solid #ffffff29;
    font-size: 14px;
    color: #fff;
    display: inline-block;
    margin-right: 5px;
    margin-top: 8px;
    -webkit-transition: color .15s ease-in-out;
    transition: color .15s ease-in-out;
    cursor: pointer;
}

.torrent-quality .download-torrent span {
    text-transform: capitalize;
}

.torrent-quality .download-torrent:hover {
    color: #919191;
}

.movie-info .imdb {
    display: flex;
    width: 90px;
    border: 1px solid #ffffff29;
    background: rgba(27, 27, 35, .4);
    border-radius: 5px;
    padding: 0 10px;
    margin-top: 40px;
}

.movie-info .imdb span {
    position: relative;
    top: 15px;
    margin-left: 10px;
    font-size: 18px;
    font-size: 700;
}

.movie-title li {
    float: left;
    padding-right: 5px;
    font-size: 18px;
    font-weight: 500;
}

.similar-movies-container {
    width: 30%;
}

.similar-movies-container p {
    margin-top: 0;
    margin-bottom: 10px;
    padding-left: 9px;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
}

.similar-movies-container .similar-movie {
    width: 35%;
    padding: 2px 9px;
    float: left
}

.card .image-link {
    display: block;
    border-radius: .3rem;
    overflow: hidden;
    position: relative;
    border: 3px solid #fff;
}

.card .image-link:hover {
    border: 3px solid #75c74e;
}

.screenshots-section {
    position: relative;
    margin-top: 50px;
    z-index: 2;
}

.summary-section {
    margin-top: 50px;
    margin-bottom: 40px;
    padding: 0 15px;
    z-index: 2;
}

.summary-section .movie-summary {
    width: 55%;
    float: left;
}

.summary-section .movie-cast {
    width: 40%;
    margin-left: 5%;
    margin-top: 50px;
    float: right;
}

.movie-summary h3 {
    margin: 0;
    color: #fff;
    font-size: 18px;
    font-style: italic;
}

.movie-summary p {
    width: 100%;
    line-height: 1.3em;
    padding: 25px 0;
}

.movie-summary div {
    font-style: italic;
    line-height: 1.3em;
    margin-bottom: 10px;
}

.movie-cast h3 {
    color: #fff;
    font-size: 18px;
    margin-top: 0;
    margin-bottom: 20px;
}

.movie-cast ul li {
    color: #fff;
    display: flex;
    border-bottom: 1px solid rgba(255, 255, 255, 0.178);
    padding-top: 8px;
}

.movie-cast ul li img {
    border-radius: 100%;
    width: 45px;
    height: 45px;
    margin-top: 5px;
    margin-bottom: 8px;
}

.movie-cast ul li span {
    color: #919191;
}

.movie-cast ul p {
    font-size: 15px;
    margin-top: 8px;
    margin-left: 10px;
}

/* -------------DOWNLOAD MODAL----------------- */

.download-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.863);
    z-index: 20;
    overflow-y: auto;
    cursor: pointer;
    visibility: hidden;
    opacity: 0;
}

.modal-active {
    visibility: visible;
    opacity: 1;
}

.download-modal .modal-container {
    position: relative;
    border-radius: 5px;
    max-width: 600px;
    width: 90%;
    background: rgb(255, 255, 255);
    margin: 90px auto;
    padding: 10px;
    cursor: auto;
}

.modal-title {
    text-align: center;
    padding-top: 30px;
    font-size: 14px;
    color: #59a336;
}

.modal-close {
    position: absolute;
    top: 0;
    right: 0;
    padding: 8px;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
}

.modal-close:hover {
    color: #333;
}

.modal-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 15px;
}

.modal-torrent {
    width: 30%;
    border-bottom: 1px solid rgb(201, 201, 201);
    text-align: center;
    padding: 8px 0;
    margin-top: 30px;
    /* display: inline-block; */
}

.modal-torrent .modal-quality {
    padding: 20px;
    max-width: 80px;
    margin: 0 auto;
    border: 2px solid rgb(138, 138, 138);
    font-size: 22px;
    font-weight: 700;
}

.modal-torrent .quality {
    font-size: 18px;
    font-weight: 700;
    color: #1d1d1d;
    padding-top: 10px;
    padding-bottom: 10px;
}

.modal-torrent .file-size {
    font-size: 18px;
    font-weight: 700;
    color: #1d1d1d;
    text-transform: uppercase;
}

.modal-torrent a {
    background-color: #6ac042;
    padding: 8px 12px;
    border-radius: 4px;
    cursor: pointer;
    -webkit-transition: all .5s;
    transition: all .5s;
}

/* -------------------------------MEDIA QUERIES---------------------------- */

@media only screen and (max-width: 1024px) {
    .movie-container .movie-poster {
        width: 40%;
    }
    .movie-container .movie-info {
        margin-left: -10%;
        width: 40%;
    }
    .similar-movies-container {
        display: none;
    }
}

@media only screen and (max-width: 768px) {
    .movie-info h1 {
        display: none;
    }
    .movie-info h2 {
        margin-top: 15px;
    }
    .hidden-movie-title {
        display: block;
    }
    .screenshots-section {
        display: block;
        margin-left: 60px;
    }
}

@media only screen and (max-width: 650px) {
    .hidden-movie-title h1 {
        margin-left: 50px;
        font-size: 30px;
    }
    .movie-info .imdb {
        margin-top: 20px;
    }
    .screenshots-section .pic {
        display: none;
    }
    .summary-section .movie-summary {
        width: 100%;
        float: none;
    }
    .summary-section .movie-cast {
        width: 80%;
        margin-left: 0;
        float: none;
    }
}

@media only screen and (max-width: 460px) {
    .movie-container {
        display: block;
    }
    .hidden-movie-title h1 {
        margin-top: 0;
        margin-left: 30px;
    }
    .movie-container .movie-poster {
        width: 60%;
        float: none;
        margin-left: 15px;
        margin-right: auto;
    }
    .movie-container .movie-info {
        margin-left: 30px;
        width: 90%;
        float: none;
    }
    .movie-info h2, .movie-info .genre-list li {
        font-size: 16px;
    }
    .movie-info .genre-list {
        margin-top: 8px;
        margin-bottom: 15px;
    }
    .movie-info .torrent-quality {
        padding-top: 10px;
    }
    .screenshots-section {
        margin-left: 15px;
    }
}
